import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';

import { WelcomeComponent } from './welcome/welcome.component';
import { PagenotfoundComponent } from './core/pagenotfound/pagenotfound.component';
import { AuthGuard } from './core/guards/auth.guard';
import { UserActivationComponent } from './user-activation/user-activation.component';


const routes: Routes = [
  { path: '', redirectTo: 'welcome', pathMatch: 'full' },
  { path: 'login', component: LoginComponent, },
  { path: 'welcome', component: WelcomeComponent },
  { path:'employee/registraton/activation/:id',component:UserActivationComponent},
  {
    path: 'admin', loadChildren: () => import('./admin/admin.module').then(module => module.AdminModule),canActivate:[AuthGuard]
  },
  {
    path: 'supreme', loadChildren: () => import('./supreme-admin/supreme-admin.module').then(module => module.SupremeAdminModule),canActivate:[AuthGuard]
  },
  {
    path: 'user', loadChildren: () => import('./employee/employee.module').then(module => module.EmployeeModule),canActivate:[AuthGuard]
  },
  {
    path: '**', component: PagenotfoundComponent
  }
];


@NgModule({
  imports: [RouterModule.forRoot(routes,{useHash:true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
