import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { LeftbarComponent } from './leftbar/leftbar.component';
import { RouterModule } from '@angular/router';
import { MyprofileComponent } from './myprofile/myprofile.component';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { JwtInterceptor } from './interceptors/jwt.interceptor';
import { ApiPrefixInterceptor } from './interceptors/api.prefix.interceptor';
import { ErrorInterceptor } from './interceptors/error.interceptor';
import { SharedServiceModule } from '@app/shared/shared.module';
import { FormsModule } from '@angular/forms';
import { RadioButtonModule } from '@app/components/public_api';

@NgModule({
  declarations: [HeaderComponent, FooterComponent, LeftbarComponent, MyprofileComponent, PagenotfoundComponent],
  imports: [
    CommonModule,
    RouterModule,
    FormsModule,
    SharedServiceModule,
    RadioButtonModule,
  ],
  exports: [
    HeaderComponent, FooterComponent, LeftbarComponent,SharedServiceModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ApiPrefixInterceptor, multi: true },
    ]
})
export class CoreModule { }
