import { Component, Input, NgZone, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { SharedService } from './shared/shared.service';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { AuthService } from './core/service/auth.service';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class AppComponent {
  isUserLoggedIn:boolean=false;
  title = 'projectX';
  showSpinner:boolean=false;
  spinnerSubscription:Subscription;
  constructor (private zone: NgZone,private authService:AuthService, private router: Router,private cdr:ChangeDetectorRef, private sharedService:SharedService) {  
    this.router.events.subscribe((event: any) => {
      if (event instanceof NavigationStart) {       
        if (!this.authService.isUserLoggedIn() || event.url.indexOf('employee/registration/activation')!=-1 || event.url=="/" || event.url.indexOf('/login')!=-1 || event.url.indexOf("/welcome")!=-1) {
          this.isUserLoggedIn= false;
        } else {
          this.isUserLoggedIn= true;
        }
        this.cdr.detectChanges();
      }
    });
  }
  ngOnInit(){

    this.spinnerSubscription=this.sharedService.loaderCounter.subscribe((counter: boolean) => {
      this.showSpinner = counter ;
      this.cdr.detectChanges();
  }); 
  }
  ngOnDestroy(){
    this.spinnerSubscription?.unsubscribe();
  }
}
