import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { WelcomeService } from './welcome.service';
import { BasicSetup } from '@app/shared/models/basic-setup-model';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { SharedService } from '@app/shared/shared.service';

@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.component.html',
  styleUrls: ['./welcome.component.css']
})
export class WelcomeComponent implements OnInit {
  formcontent: boolean = false;
  basicSetupForm: FormGroup;
  basicSetup: BasicSetup;
  lstEmployees: any[] = [];
  getStartedDisplay: string = 'none';
  constructor(private router: Router, private fb: FormBuilder,private sharedService:SharedService, private welcomeService: WelcomeService) {
    this.basicSetup = new BasicSetup();
    this.basicSetupForm = this.fb.group({
      title: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.minLength(1), Validators.maxLength(150)]),
      noOfEmployees: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.minLength(1), Validators.maxLength(150)]),
      workEmail: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.minLength(1), Validators.email,Validators.maxLength(150)]),
      firstName: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.minLength(3), Validators.maxLength(50)]),
      lastName: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.minLength(3), Validators.maxLength(50)]),
      organizationName: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.minLength(1), Validators.maxLength(500)]),
      workPhone: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.maxLength(10)])
    });
  }

  ngOnInit(): void {
    this.formcontent = false;
    this.lstEmployees = [{ value: '1-5', label: '1 - 5' }, { value: '6-10', label: '6 - 10' }, { value: '11-25', label: '11 - 25' }, { value: '26-150', label: '26 - 150' }, { value: '151+', label: '151+' }]
  }
  onLoginClick() {
    this.router.navigate(['login']);
  }
  onSignUpClick() {

  }
  onbackClick() {
    this.formcontent = false;
  }
  formClick(item) {
    this.basicSetupForm.get('noOfEmployees').setValue(item.value)
    this.formcontent = true;
  }

  validateWorkEmail(){
    let workEmail = this.basicSetupForm.get('workEmail').value;
    this.welcomeService.validateDomain(workEmail).subscribe(res => {
      if (res['message'] == "Valid Domain") {       
      }
      else{
        this.sharedService.add({
          severity: 'error',
          summary: 'Error',
          detail: res['message']     
        })   
        this.basicSetupForm.get('workEmail').setValue("");        
      }
    })
  }

  onGetStartedClick() {
    this.formcontent = false;
    this.getStartedDisplay = 'block';
    this.hiddenScroll();
  }
  closeAddmodal(){
    this.formcontent = false;
    this.getStartedDisplay = 'none';
    this.displayScroll();
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add('modal-open');
      }
    }
    catch (ex) {

    }
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove('modal-open');
      }
    }
    catch (ex) {
    }
  }
  onFormSubmit() {
    if (this.basicSetupForm.valid) {
      this.basicSetup.noOfEmployees = this.basicSetupForm.get('noOfEmployees').value;
      this.basicSetup.title = this.basicSetupForm.get('title').value;
      this.basicSetup.workEmail = this.basicSetupForm.get('workEmail').value;
      this.basicSetup.firstName = this.basicSetupForm.get('firstName').value;
      this.basicSetup.lastName = this.basicSetupForm.get('lastName').value;
      this.basicSetup.organizationName = this.basicSetupForm.get('organizationName').value;
      this.basicSetup.workPhone = this.basicSetupForm.get('workPhone').value;
      this.welcomeService.basicSetUp(this.basicSetup).subscribe(res => {
        if (res['message'] == "Basic Setup completed successfully") {
          this.sharedService.add({
            severity: 'success',
            summary: 'Success',
            detail: res['message']     
          })    
          this.closeAddmodal();
          //this.router.navigate(['login']);
        }
        else{
          this.sharedService.add({
            severity: 'error',
            summary: 'Error',
            detail: res['message']     
          })   
        }
      })
    }
  }
}
