import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiResponse } from '@app/shared/models/api-response.model';
import { Department } from '@app/shared/models/department.model';
import { Profile, Experience, Education } from '@app/shared/models/profile.model';
import { Designation } from '@app/shared/models/designation.model';
import { Employee } from '@app/shared/models/employee.model';

@Injectable({
    providedIn: 'root'
})
export class MyProfileService {
    constructor(private http: HttpClient) {
    }
    getProfile(employeeDetailsId: number) {
        return this.http.get<ApiResponse<Profile>>(`api/profile/${employeeDetailsId}`);
    }
    saveProfile(profile: Profile) {
        return this.http.post<ApiResponse<Profile>>(`api/profile/save`, profile);
    }

    getEducations(employeeDetailsId: number) {
        return this.http.get<ApiResponse<Education>>(`api/profile/educations/${employeeDetailsId}`);
    }
    getExperiences(employeeDetailsId: number) {
        return this.http.get<ApiResponse<Experience>>(`api/profile/experiences/${employeeDetailsId}`);
    }
    saveEducations(educations: Education[]) {
        return this.http.post<ApiResponse<Education>>(`api/profile/educations/save`,educations);
    }
    saveExperiences(experiences: Experience[]) {
        return this.http.post<ApiResponse<Experience>>(`api/profile/experiences/save`,experiences);
    }
    getAllDesignations(){
        return this.http.get<ApiResponse<Designation>>('api/designation/all?status=1');
    }
    getAllDepartments(){
        return this.http.get<ApiResponse<Department>>('api/department/all?status=1');
    }
    getEmployee(employeeDetailsId: string) {
        return this.http.get<ApiResponse<Employee>>(`api/employee/${employeeDetailsId}`);
    }
    getReportingToUsers(){        
        return this.http.get<ApiResponse<Employee>>(`api/employee/reportingto/all?status=1`);
    }
}