import { Component, OnInit } from '@angular/core';
import { SharedService } from '@app/shared/shared.service';
import { MyProfileService } from './myprofile.service';
import { User } from '@app/shared/models/user.model';
import { AuthService } from '../service/auth.service';
import { Profile, Experience, Education } from '@app/shared/models/profile.model';
import { ActivatedRoute } from '@angular/router';
import { Department } from '@app/shared/models/department.model';
import { Designation } from '@app/shared/models/designation.model';
import {  Gender} from "@app/shared/enums/gender.enum";
import {  MaritalStatus} from "@app/shared/enums/maritalstatus.enum";
@Component({
  selector: 'app-myprofile',
  templateUrl: './myprofile.component.html',
  styleUrls: ['./myprofile.component.css']
})
export class MyprofileComponent implements OnInit {

  clientErrorMsg: any;
  public profileinfo: string = "none";
  educationInfo: string = 'none';
  experienceInfo: string = 'none';
  user: User;
  profile: Profile;
  saveProfile:Profile;
  state: any;  
  get Gender() { return Gender; }
  get MaritalStatus() { return MaritalStatus; }
  educations: Education[] = [];
  experiences: Experience[] = [];
  maritalStatuses:any[]=[];
  genders:any[]=[];
  departments:any[]=[];
  filterDepartments:any[]=[];
  filterDesignation:any[]=[];
  designations:any[]=[]
  reportingToUserNames:any[]=[];
  errorLabel:string='';
  constructor(private sharedService:SharedService,private authService: AuthService, private activatedRoute: ActivatedRoute, private profileService: MyProfileService) {
    this.user = this.authService.currentUser;
    this.profile = new Profile();
  }

  ngOnInit(): void {
    this.maritalStatuses = [
      { label: 'Select Marital status', value: null },
      { label: 'Single', value: "1" },
      { label: 'Married', value: "2"}

    ];
    this.genders = [
      { label: 'Select Gender', value: null },
      { label: 'Male', value: "1" },
      { label: 'Female', value: "2" }

    ];
    // this.departments.push({label:'Select Department',value:null});
    // this.designations.push({label:'Select Designation',value:null});
    this.reportingToUserNames.push({label:'Select Reports To',value:null});
    this.profileService.getAllDepartments().subscribe(res=>{   
      if(res.responsePayload){
        res.responsePayload.forEach(value=>{
          this.departments.push({label:value.department,value:value.departmentDetailsId})
        })
      }
    })
    this.profileService.getAllDesignations().subscribe(res=>{
      if(res.responsePayload){
        res.responsePayload.forEach(value=>{
          this.designations.push({label:value.designation,value:value.designationDetailsId})
        })
      }
    });
    this.profileService.getReportingToUsers().subscribe(res=>{
      if(res.responsePayload){
        res.responsePayload.forEach(value=>{
          this.reportingToUserNames.push({label:value.fullName,value:value.employeeDetailsId})
        })
      }
    })

    let userId = null;
    this.activatedRoute.queryParams.subscribe(params => {
      this.state = window.history.state;
      console.log(this.state, this.user);      
      if (this.state && this.state.userDetailsId) {
        userId = this.state.userDetailsId
      }
      else {
        userId = this.user.employeeDetailsId;
      }
      this.profile.employeeDetailsId = userId;
      this.getProfile(userId);
      this.getEducations(userId);
      this.getExperiences(userId);
    })

  }

  departmentsList($event){
    let query = $event.query;
    // let filtered : any[] = [];
    this.filterDepartments = []
    for(let i = 0; i < this.departments.length; i++) {
        let country = this.departments[i];
        if (country.label.toLowerCase().indexOf(query.toLowerCase()) == 0) {
          this.filterDepartments.push(country);
        }
    }
    console.log(this.filterDepartments);        
  }

  designationList($event){
    let query = $event.query;
    // let filtered : any[] = [];
    this.filterDesignation = []
    for(let i = 0; i < this.designations.length; i++) {
        let designation = this.designations[i];
        if (designation.label.toLowerCase().indexOf(query.toLowerCase()) == 0) {
          this.filterDesignation.push(designation);
        }
    }
    console.log(this.filterDesignation);        
  }   

  getProfile(employeeDetailsId) {
    this.profileService.getProfile(employeeDetailsId).subscribe(res => {

      if (res.message == "Profile Details") {
        this.profile = res.responsePayload;
        return;
      }
      this.profileService.getEmployee(employeeDetailsId).subscribe(res=>{
        if(res.message=="Employee Details"){
          this.profile.firstName=res.responsePayload.firstName;
          this.profile.lastName=res.responsePayload.lastName;
          this.profile.fullName=res.responsePayload.fullName;
          this.profile.email=res.responsePayload.email;
          this.profile.phoneNumber=res.responsePayload.phoneNumber;
          this.profile.employeeDetailsId=res.responsePayload.employeeDetailsId;          
          this.profile.reportingToUserName=" ";
          this.profile.address=" "
        }
      });
     if (res.message == `Profile Details not found for the employeeDetailsId :${this.user.employeeDetailsId}`) {

      }

    });
  }
  editprofile(type?) {
    this.saveProfile= Object.assign({},this.profile);
    this.profileinfo = "block";
    if(type=='emergencyContact'){
     setTimeout(() => {
      document.getElementById('primaryContactName')?.focus();
     }, 100);
    }
    else  if(type=='personalInfo'){
      setTimeout(() => {
      document.getElementById('passportNo')?.focus();       
      }, 100); 
    }
    this.hiddenScroll();
  }
  educationInfoClick() {
    this.educationInfo = 'block'
    this.hiddenScroll();
  }
  onEducationSubmit() {
    this.errorLabel='';
    this.profileService.saveEducations(this.educations).subscribe(res => {
      if (res.message == "Educations saved successfully") {
        this.closeAddmodal();
      }
      else{
        this.errorLabel=res.message;
      }
    },error=>{      
        this.errorLabel=error;      
    });
  }
  onProfileSubmit(){        
    this.errorLabel=''; 
    if(this.saveProfile.departmentDetails && this.saveProfile.departmentDetails instanceof Object) {
      this.saveProfile.departmentDetailsId = this.saveProfile.departmentDetails.value;
    }else if(this.saveProfile.departmentDetails){
      this.saveProfile.departmentDetailsId = 0;
      this.saveProfile.department = this.saveProfile.departmentDetails;
    }
    if(this.saveProfile.designationDetails && this.saveProfile.designationDetails instanceof Object) {
      this.saveProfile.designationDetailsId = this.saveProfile.designationDetails.value;
    }else if(this.saveProfile.designationDetails){
      this.saveProfile.designationDetailsId = 0;
      this.saveProfile.designation = this.saveProfile.designationDetails;
    }

    console.log(this.saveProfile);    
    this.profileService.saveProfile(this.saveProfile).subscribe(res => {
      if (res.message == "Profile saved successfully") {
        this.sharedService.add({severity:'success',summary:'Success',detail:res.message})
        this.closeAddmodal();
        this.getProfile( this.getProfileId());
      }
      else{
        this.errorLabel=res.message;
      }
    },errror=>{
      this.errorLabel=errror;
    });
  }
  onRemoveEducation(education: Education) {
    if (this.educations.length > 1) {
      let index = this.educations.indexOf(education);
      this.educations.splice(index, 1);
    }
  }
  onAddMoreEducation() {
    let education = Object.assign({},new Education());

    education.employeeDetailsId = this.getProfileId();

    education.status = 1;
    this.educations.push(education);
  }
  experienceClick() {
    this.experienceInfo = 'block'
    this.hiddenScroll();
  }
  onExperienceSubmit() {
    this.errorLabel='';
    this.profileService.saveExperiences(this.experiences).subscribe(res => {
      if (res.message == "Experiences saved successfully") {
        this.closeAddmodal();
      }
      else{
        this.errorLabel=res.message;
      }
    },error=>{      
        this.errorLabel=error;      
    });
  }
  onRemoveExperience(experience: Experience) {
    if (this.experiences.length > 1) {
      let index = this.experiences.indexOf(experience);
      this.experiences.splice(index, 1);
    }
  }
  getProfileId() {
    if (this.state && this.state.userDetailsId) {
      return this.state.userDetailsId;
    }
    else {
      return this.user.employeeDetailsId;
    }
  }
  onAddMoreExperience() {
    let experience = Object.assign({},new Experience());

    experience.employeeDetailsId = this.getProfileId();

    experience.status = 1;
    this.experiences.push(experience);
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add('modal-open');
      }
    }
    catch (ex) {
      this.clientErrorMsg(ex, 'hiddenScroll');
    }
  }
  getEducations(employeeDetailsId: number) {
    this.profileService.getEducations(employeeDetailsId).subscribe(res => {
      if (res.message == "Educations") {
        this.educations = res.responsePayload;
        return;
      }
      else if (res.message == `Profile Details not found for the employeeDetailsId :${employeeDetailsId}`) {

      }
      let education = Object.assign({},new Education());
      education.employeeDetailsId =this.getProfileId();
      education.status = 1;
      this.educations.push(education);
    });
  }
  getExperiences(employeeDetailsId: number) {
    this.profileService.getExperiences(employeeDetailsId).subscribe(res => {
      if (res.message == "Experiences") {
        this.experiences = res.responsePayload;
        return;
      }
      else if (res.message == `Profile Details not found for the employeeDetailsId :${employeeDetailsId}`) {

      }
      let experience =Object.assign({}, new Experience());
      experience.employeeDetailsId = this.getProfileId();
      experience.status = 1;
      this.experiences.push(experience);
    });
  }
  saveEducations(educations: Education[]) {
    this.profileService.saveEducations(this.profile.educations).subscribe(res => {
      if (res.message == "Educations") {
        this.profile = res.responsePayload;
      }
      else if (res.message == `Profile Details not found for the employeeDetailsId :${this.user.employeeDetailsId}`) {

      }

    });
  }
  saveExperiences(experiences: Experience[]) {
    this.profileService.saveExperiences(this.profile.experiences).subscribe(res => {
      if (res.message == "Experiences saved successfully") {
        this.profile = res.responsePayload;
      }
      else if (res.message == `Profile Details not found for the employeeDetailsId :${this.user.employeeDetailsId}`) {

      }

    });
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove('modal-open');
      }
    }
    catch (ex) {
      this.clientErrorMsg(ex, 'displayScroll');
    }
  }
  closeAddmodal() {
    this.profileinfo = "none";
    this.educationInfo = 'none';
    this.experienceInfo = 'none';
    this.errorLabel='';
    this.displayScroll();
  }
}
