import { Component, OnInit, Input } from '@angular/core';
import { AuthService } from '../service/auth.service';
import { Role } from '@app/shared/enums/role.enum';
import { Modules } from '@app/shared/enums/modules.enum';

@Component({
    selector: 'app-leftbar',
    templateUrl: './leftbar.component.html',
    styleUrls: ['./leftbar.component.css']
})
export class LeftbarComponent implements OnInit {

    constructor(private authService: AuthService) { }
    lstMenus: any[] = [];
    ngOnInit(): void {
        //this.fillNavMenus(this.authService.currentUser.role);
        let user = this.authService.currentUser;
        this.lstMenus.push({
            routeName: `${Role[user.roleId]} Dashboard`, iconColor: 'icon-color', routeUrl: Role[user.roleId], iconName: 'la la-dashboard', children: [], disabled: false
        })
        if(user.roleId == 1){
            this.lstMenus.push({
                routeName: `Organization`, iconColor: 'icon-color', routeUrl: 'supreme/organization', iconName: 'la la-dashboard', children: [], disabled: false
            })
        }else if(user.roleId == 3){
            this.lstMenus.push({
                routeName: `Document Category`, iconColor: 'icon-color', routeUrl: 'admin/category', iconName: 'la la-dashboard', children: [], disabled: false
            })
        }
        if(user.accessibleModules){
            for (const module of user.accessibleModules) {  
                if(Modules[module]){
                    this.lstMenus.push({
                        routeName: Modules[module], iconColor: 'icon-color', routeUrl: `${Role[user.roleId].toLowerCase()}/${Modules[module].toLowerCase()}`, iconName: this.getMenuIcon(module), children: [], disabled: false
                    })
                }         
            }
            console.log(this.lstMenus);
            
        }
    }
    getMenuIcon(module) {
        switch (module) {
            case Modules.COMPANY.toUpperCase():
                return 'la la-company'
            case Modules.DEPARTMENTS.toUpperCase():
                return 'la la-building'
            case Modules.DESIGNATION.toUpperCase():
                return 'la la-user-plus'
            case Modules.EMPLOYEES.toUpperCase():
                return 'la la-user'
            case Modules.ORGANIZATIONS.toUpperCase():
                return 'la la-organization'
            case Modules.DOCUMENTS.toUpperCase():
                return 'la la-document'
            case Modules.FEEDBACK.toUpperCase():
                return 'la la-feedback'
            case Modules.I9.toUpperCase():
                return 'la la-i9'
            case Modules.NOTES.toUpperCase():
                return 'la la-notes'
            case Modules.PAYSTUBS.toUpperCase():
                return 'la la-paystubs'
            case Modules.PROFILE.toUpperCase():
                return 'la la-profile'
            case Modules.PROJECTS.toUpperCase():
                return 'la la-project'
            case Modules.TIMESHEETS.toUpperCase():
                return 'la la-timesheet'
            case Modules.W4.toUpperCase():
                return 'la la-w4'
        }
    }
    fillNavMenus(role) {

        switch (role) {
            case Role[Role.Admin].toUpperCase(): {
                this.lstMenus = [
                    {
                        routeName: 'Admin Dashboard', iconColor: 'icon-color', routeUrl: 'admin', iconName: 'la la-dashboard', children: [], disabled: false
                    },
                    {
                        routeName: 'Employees', routeUrl: 'admin/employees', iconColor: 'icon-color', iconName: 'la la-user', children: [], disabled: false
                    },

                    {
                        routeName: 'Departments', iconColor: 'icon-color', routeUrl: 'admin/departments', iconName: 'la la-building'
                        , disabled: false
                    },

                    {
                        routeName: 'Designations', routeUrl: 'admin/designations', iconColor: 'icon-color', iconName: 'la la-user-plus'
                        , disabled: false
                    },
                ]
                break;
            }
            case Role[Role.User].toUpperCase():
                {
                    this.lstMenus = [

                        {
                            routeName: 'User Dashboard', iconColor: 'icon-color', routeUrl: 'user', iconName: 'la la-dashboard',
                            disabled: false
                        },
                    ]
                    break;
                }
        }
    }
}

